import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

function UpgradeDiscraption() {
    return (
        <Container className="text-end">
            <Row className='py-100'>
                <Col md={6} xs={12}>
                    <h3 className="my-5 overflow-visible">      </h3>
                    <p className="font18 line-height-35">
                               Twitch YouTube  Kick    -   Just Chatting  IRL       .                           ASMR    .       OBS Studio Streamlabs  StreamElements               .        !
                    </p>
                    <SectionPopularSearches />
                </Col>
                <Col md={6} xs={12}>
                    <Row className='g-5'>
                        <BoxHeader image="/image/section/feature-chat-box-overlays-1.webp" text="  " />
                        <BoxHeader image="/image/section/feature-all-products-1.webp" text=" " />
                        <BoxHeader image="/image/section/feature-stream-alerts-1.webp" text=" " />
                        <BoxHeader image="/image/section/feature-overlay-packs-1.webp" text=" " />

                    </Row>
                </Col>
            </Row>
        </Container>
    );
}
function BoxHeader({ image, text }) {
    return (
        <Col className='' xs={6}>
            <div style={{ backgroundColor: "#f7f7f7" }} className=' p-5 rounded-5 d-flex flex-column gap-4 align-items-center'>
                <img src={image} className="w-100 rounded-4" alt={image} />
                <span className='font16 text-black fw-bold'>{text}</span>
            </div>
        </Col>

    );
}
function SectionPopularSearches() {
    return (
        <div className='py-100'>
            <h3 className='mb-5'>   </h3>
            <div className="d-flex gap-4 flex-wrap ">
                <BoxSearch text=" " />
                <BoxSearch text="     " />
                <BoxSearch text="  " />
                <BoxSearch text="  " />
                <BoxSearch text="  " />
                <BoxSearch text=" " />
            </div>
        </div>

    )
}
function BoxSearch({ text }) {
    return (
        <div className="border border-black border-3 px-5 py-4 rounded-4 font14">
            {text}
        </div>
    )
}
export default UpgradeDiscraption;